import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllExitRequestsComponent } from './exit-requests/all-exit-requests/all-exit-requests.component';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';

const routes: Routes = [
  {
    path: "all-exit-requests",
    component: AllExitRequestsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ExitManagementRoutingModule { }
