import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Subject, takeUntil, Subscription } from 'rxjs';
import { NotificationServiceService } from 'src/@core/Models/Notification/notification-service.service';
import { ExitManagementService } from 'src/app/erp-hr/data/exit-requests/exit-requests.service';
import { EmployeesLookupComponent } from 'src/app/erp-hr/hr-lookups/configurations-lookups/employees-lookup/employees-lookup.component';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';
import { AllEmployeesComponent } from '../../../employees/all-employees/all-employees.component';

@Component({
  selector: 'app-manage-exit-interview-shedule',
  templateUrl: './manage-exit-interview-shedule.component.html',
  styleUrls: ['./manage-exit-interview-shedule.component.sass']
})
export class ManageExitInterviewSheduleComponent implements OnInit {

  mngForm: FormGroup;

  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  pageFunction: string = "Add Interview Schedule";

  hideSubmit: boolean = false;
  isLoading: boolean = true;

  trainingTypes: string[] = ["Physical", "Virtual"];

  interviewModes = [
    { name: "In-person" },
    { name: "Virtual" },
    { name: "Phone" },
  ];

  constructor(
    public dialogRef: MatDialogRef<AllEmployeesComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private exitManagementService: ExitManagementService,
    private dialog: MatDialog,
    private notificationAPI: NotificationServiceService
  ) {}

  ngOnInit(): void {
    console.log("LOG DATA = ", this.data);
    this.pageFunction = this.data.action;
    this.formData = this.data.record;
    this.generateForm(this.formData);

    this.getPage();
  }
  ngAfterViewInit() {
    if (this.data.action !== "Add Interview Schedule") {
      this.generateForm(this.formData);
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  disableTransactionType: boolean = true;
  getPage() {
    this.disableTransactionType = true;
    console.log("this.data.action :", this.data.action);
    if (this.data.action === "Add Interview Schedule") {
      this.isLoading = false;
      this.generateForm(this.data.record);
    } else if (this.data.action === "Update Interview Schedule") {
      this.generateForm(this.data.record);
    } else if (this.data.action === "View Interview Schedule") {
      this.generateForm(this.data.record);
      this.activateViewMode();
    }
    this.isLoading = false;

    if(this.formData.exitInterviewers.length>0){
      this.dataSource.data = this.formData.exitInterviewers
      this.refresh()
    }
  }


  // {
  //   "id": 0,
  //   "interviewDate": "2024-05-20T08:32:39.214Z",
  //   "startTime": "string",
  //   "interviewVenue": "string",
  //   "interviewers": "string"
  // }
  formData: any;
  generateForm(formData?): void {
    this.mngForm = this.fb.group({
      id: [formData?.id ?? ""],
      interviewVenue: [formData?.interviewVenue ?? "", [Validators.required]],
      interviewDate: [formData?.interviewDate ?? "", [Validators.required]],
      startTime: [formData?.startTime ?? "", [Validators.required]],
      exitInterviewers: [formData?.exitInterviewers ?? []],
    });
  }

  viewMode: boolean = false;
  activateViewMode() {
    this.viewMode = true;
  }

  //********************************************************************************************************************** */

  //********************************************************************************************************************** */
  dataSource = new MatTableDataSource<any>([]);
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  displayedColumns: string[] = ["id", "empPf", "empName", "action"];
  tranForm: FormGroup;
  showParTranForm = false;
  parTranAction: string = "";
  selectedParTranIndex: number;
  debitTotal: number = 0;
  creditTotal: number = 0;
  balance: number = 0;

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
  // generateSubForm(formData?): void {
  //   this.tranForm = this.fb.group({
  //     id: [formData?.id ?? ""],
  //     description: [formData?.narration ?? "", [Validators.required]],
  //   });
  // }
  resetFormAndHide() {
    this.mngForm.patchValue({
      exitInterviewers: this.dataSource.data,
    });
    this.tranForm.reset();
    this.showParTranForm = false;
  }

  addParTran() {
    // this.parTranAction = "Add";
    // this.showParTranForm = true;
    this.employeeLookup();
  }

  refresh() {
    this.dataSource.data = [...this.dataSource.data];
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    this.mngForm.patchValue({
      exitInterviewers: this.dataSource.data,
    });
  }

  editParTran(data: any, index: number) {
    this.parTranAction = "Update";
    this.selectedParTranIndex = index;
    // this.generateSubForm(data);
    this.showParTranForm = true;
  }

  pushToDataSource() {
    if (this.parTranAction === "Add") {
      this.dataSource.data.push(this.tranForm.value);
    } else if (this.parTranAction === "Update") {
      let indexToUpdate: number;

      if (this.tranForm.value.id) {
        indexToUpdate = this.dataSource.data.findIndex(
          (item) => item.id === this.tranForm.value.id
        );
      } else {
        indexToUpdate = this.selectedParTranIndex;
      }

      if (indexToUpdate !== -1) {
        this.dataSource.data[indexToUpdate] = this.tranForm.value;
      }
    }

    this.resetFormAndHide();
    this.refresh();
  }

  cancelParTran() {
    this.resetFormAndHide();
  }

  deleteParTran(index: number) {
    this.dataSource.data.splice(index, 1);
    this.refresh();
  }

  // **************************************************************************************************

  posting: boolean = false;

  response: any;
  submit() {
    console.log("this.mngForm.value: ", this.mngForm.value);
    this.posting = true;
    this.exitManagementService
      .scheduleExitInterview(this.mngForm.value)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          this.response = res;
          console.log("res: ", res);
          if (res.statusCode == 200) {
            this.snackbar.showNotification("snackbar-success", res.message);
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {
          this.posting = false;
          setTimeout(() => {
            this.onNoClick();
          }, 3000);
        },
      }),
      Subscription;
  }

  onNoClick(): void {
    this.dialogRef.close({ event: "close", data: this.response });
  }

  selectedItem: any;
  employeeLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: "MultipleEmployee",
      selected: this.selectedItem,
    };
    const dialogRef = this.dialog.open(EmployeesLookupComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((res) => {
      if (res && res.data && res.data.length > 0) {
        res.data.forEach((employeeInfo) => {
          const empName =
            employeeInfo.firstName +
            " " +
            employeeInfo.middleName +
            " " +
            employeeInfo.lastName;
          const empPf = employeeInfo.empNo;

          if (this.dataSource.data) {
            const existingItemIndex = this.dataSource.data.findIndex(
              (item) => item.empPf === empPf || item.empName === empName
            );

            if (existingItemIndex !== -1) {
              this.dataSource.data[existingItemIndex] = {
                ...this.dataSource.data[existingItemIndex],
                empPf,
                empName,
              };
            } else {
              this.dataSource.data.push({
                empPf,
                empName,
              });
            }
          }
        });

        this.refresh();

        
      }
    });
  }
}
